<template>
  <div class="Bar">
    <div
      ref="myChart"
      :style="{ width: '600px', height: '350px', margin: '21px auto 0'}"
    ></div>
    <!-- <p>{{ datas }}</p> -->
  </div>
</template>
<script>
// 引入基本模板
let echarts = require("echarts/lib/echarts");
// 引入柱状图组件
require("echarts/lib/chart/bar");
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
export default {
  name: "hello",
  //datas为父组件ChartsData从后台请求到的经过处理后的数据，每次传一个组件的数据，返回一个组件的图表
  props: ["datas"],
  data() {
    return {
      chartsBody: {
        
        title: { text: "" },
        tooltip: {
          trigger: "item",
          formatter: "{b}: {c} ",
        },
        xAxis: {
          offset:10,
          // show:false,
          type: "category",
          data: [],
          axisLabel: {
            //这个是倾斜角度，也是考虑到文字过多的时候，方式覆盖采用倾斜
            rotate: 5,
            //这里是考虑到x轴文件过多的时候设置的，如果文字太多，默认是间隔显示，设置为0，标示全部显示，当然，如果x轴都不显示，那也就没有意义了
            interval: 0,
          },
        },
        yAxis: {
          type: "value",
          data: [],
        },
        series: [
          {
            name: "",
            type: "bar",
            // barWidth: 100,
            data: this.datas.datay,
            //主题颜色
            // barWidth:70,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: "top",
                  formatter: "{c}", //这是关键，在需要的地方加上就行了
                },
                //这里是重点
                color: function (params) {
                  //注意，如果颜色太少的话，后面颜色不会自动循环，最好多定义几个颜色
                  var colorList = [
                    "#f16b79",
                    "#73b9f3",
                    "#39d2d9",
                    "#7bd473",
                    "#b18160",
                    "#FFB6C1",
                    "#8B008B",
                    "#4169E1",
                    "#00FF7F",
                    "#FFA500",
                  ];
                  return colorList[params.dataIndex];
                },
              },
            },
          },
        ],
      },
      chartsTitle: this.datas.title,
      chartsXAxis: this.datas.datax,
      chartsDatas: this.datas.datay,
    };
  },
  mounted() {
    this.inputData();
    this.drawLine();
  },
  computed: {
    // presentNum() {},
  },
  methods: {
    inputData() {
      // console.log(this.datas.datay);
      this.chartsBody.title.text = this.chartsTitle + ":";
      this.chartsBody.xAxis.data = this.chartsXAxis;
      // var sum = 0;
      // for (var i = 0; i < this.datas.datay.length; i++) {
      //   sum = sum + this.datas.datay[i];
      // }
      // console.log(sum);
    },

    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(this.$refs.myChart);
      myChart.setOption(this.chartsBody);
    },
  },
};
</script>

<style>
.Bar {
  float: center;
}
</style>