<template>
  <div>
    <div
      ref="myChart"
      :style="{ width: '600px', height: '350px', margin: '21px auto 0' }"
    ></div>
    <!-- <p>{{ ArrData1 }}</p> -->
  </div>
</template>
<script>
// 引入基本模板
let echarts = require("echarts/lib/echarts");
// 引入柱状图组件
require("echarts/lib/chart/bar");
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
export default {
  name: "hello",
  //datas为父组件ChartsData从后台请求到的经过处理后的数据，每次传一个组件的数据，返回一个组件的图表
  props: ["datas"],
  data() {
    return {
      chartsBody: {
        title: { text: "" },
        legend: {
          show: true,
          icon: "circle",
          orient: "vertical",
          left: "right",
          data: [],
        },
        tooltip: {
          trigger: "item",
          formatter: "{b}: {c} ({d}%)",
        },
        series: [
          {
            type: "pie",
            radius: "55%",
            data: [],
            itemStyle: {
              normal: {
                //这里是重点
                color: function (params) {
                  //注意，如果颜色太少的话，后面颜色不会自动循环，最好多定义几个颜色
                 var colorList = [
                    "#f16b79",
                    "#73b9f3",
                    "#39d2d9",
                    "#7bd473",
                    "#b18160",
                    "#FFA500",
                    "#FFB6C1",
                    "#8B008B",
                    "#4169E1",
                    "#00FF7F",
                  ];
                  return colorList[params.dataIndex];
                },
              },
            },
          },
        ],
      },
      chartsTitle: this.datas.title + ":",
    };
  },
  computed: {},
  created() {},
  mounted() {
    this.inputData();
    this.drawLine();
  },
  methods: {
    inputData() {
      var ArrData = [];
      var leftBarTitle = [];
      for (var i = 0; i < this.datas.datax.length; i++) {
        leftBarTitle.push(this.datas.datax[i]);
        var Arr = [];
        var paramValue = {};
        var paramName = {};
        paramValue["value"] = this.datas.datay[i];
        paramName["name"] = this.datas.datax[i];
        Arr.push(paramValue);
        Arr.push(paramName);
        var newObj = {};
        Arr.forEach((item) => {
          newObj = { ...newObj, ...item };
        });
        ArrData.push(newObj);
      }
      // console.log(leftBarTitle);
      this.chartsBody.legend.data = leftBarTitle;
      this.chartsBody.series[0].data = ArrData;
      this.chartsBody.title.text = this.chartsTitle;
    },
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(this.$refs.myChart);
      myChart.setOption(this.chartsBody);
    },
  },
};
</script>

