<template>
<div class="charts-overflow">
    <!-- 循环得id和下标item -->
    <div class="co-1-chartsHome" v-for="(item, index) in chartIdList" :key="'charts_' + index">
        <div class="co-1-charts" :id="item.id" :style="{ height: '400px' }">
            <div class="co-1-button">
                <div class="co-1-button one">
                    <el-button type="text" class="co-1-button a" @click="switchBar(index)">柱状图</el-button>
                    <el-button type="text" class="co-1-button a" @click="switchPie(index)">饼状图</el-button>
                </div>
            </div>
            <!-- 画   echarts -->
            <component class="chartCom" style="margin: auto 0" :is="chartsDefaultTypes[index].type" :datas="chartsOption[index]"></component>
        </div>
        <!-- 画   table -->
        <div class="co-1-details">
            <div>
                <component :is="tableType" :datas="tableOption[index]"></component>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import Bar from "@/components/EditPageComp/chartcomp/Bar.vue";
import Pie from "@/components/EditPageComp/chartcomp/Pie.vue";
import ctable from "@/components/EditPageComp/chartcomp/ctable.vue";
import {
    gettableData,
    gettableColumn
} from "@/network/formdata.js";
export default {
    components: {
        Bar,
        Pie,
        ctable,
    },
    data() {
        return {
            tableType: "ctable",
            formId: "",
            chartsDefaultTypes: [],
            chartIdList: [],
            chartsOption: [],
            tableOption: [],
        };
    },
    created() {
        sessionStorage.setItem("TagActiveName", "fourth");
        this.formId = this.$route.params.id;
        this.drawDivId();
    },
    methods: {
        switchPie(index) {
            this.chartsDefaultTypes[index].type = "Pie";
        },
        switchBar(index) {
            this.chartsDefaultTypes[index].type = "Bar";
        },
        drawDivId() {
            //数组存放组件长度
            var arrChartsLength = [];
            //遍历组件，动态生成id
            var arrOptionData = [];
            //问题选项名数组
            var templateContent = [];
            //问题题目
            var titleArr = [];
            this.axios
                .all([gettableData(this.formId), gettableColumn(this.formId)])
                .then(
                    this.axios.spread((res1, res2) => {
                        //dataBody 请求gettableData返回的数据
                        var dataBody = res1.data;
                        //dataHead 请求gettableColumn返回的数据
                        var dataHead = res2.data;

                        console.log("########《dataHead表头数据》########");
                        console.log(dataHead);
                        console.log("########《dataBody    表身数据》########");
                        console.log(dataBody);

                        // 每一题的答案都放入一个ans数组
                        var ans = "";
                        //arrAns 每一个ans数组都放入arrAns数组
                        var arrAns = [];
                        //最后提供给echarts datayValue 的答案数组
                        var dataValue = [];

                        //================================================造答案=================================================//
                        for (
                            let checkOptionNum = 0; checkOptionNum < dataHead.length; checkOptionNum++
                        ) {
                            arrAns.push([]);
                        }

                        //因为表身最外面有一层，所以先循环一次拿到每一个用户的位置
                        for (let findUser in dataBody) {
                            //循环表头 拿 表头每一个问题的formTemplateId，
                            for (let findQuestionAll in dataHead) {
                                for (let findUserAnsAll in dataBody[findUser].dataDetailsList) {
                                    //循环每一个用户，拿到每一个用户的所有答案
                                    //判断表身formTemplateId和表头formTemplateId是否相等，且不是非选择题类型
                                    if (
                                        dataBody[findUser].dataDetailsList[findUserAnsAll]
                                        .formTemplateId ==
                                        dataHead[findQuestionAll].formTemplateId &&
                                        dataHead[findQuestionAll].templateName != "textbox"
                                    ) {
                                        //如果判断通过，则拿表头中的问题类型templateName 开始判断是什么选择类型【有单选（json）和多选（数组[]）】
                                        //----------------------------------1、如果为单选 则处理json object
                                        if (
                                            dataHead[findQuestionAll].templateName == "radio" ||
                                            dataHead[findQuestionAll].templateName == "select" ||
                                            dataHead[findQuestionAll].templateName == "picradio"
                                        ) {
                                            //如果不为空值或者空数组，做处理
                                            if (
                                                dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                .dataContent != "[]" &&
                                                dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                .dataContent != "" &&
                                                dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                .dataContent != "null"
                                            ) {
                                                //因为sql返回的是json字符串，所以要进行处理
                                                ans = JSON.parse(
                                                    dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                    .dataContent
                                                );
                                                ans = ans.value;
                                                arrAns[findQuestionAll].push(ans);
                                            } else {
                                                //空值则赋一个 ""
                                                ans = "";
                                                arrAns[findQuestionAll].push(ans);
                                            }
                                        } else if (
                                            //----------------------------------2、如果为多选 则处理array []
                                            dataHead[findQuestionAll].templateName == "checkbox" ||
                                            dataHead[findQuestionAll].templateName == "piccheckbox"
                                        ) {
                                            //如果为不为空值或者空数组，做处理
                                            if (
                                                dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                .dataContent != "[]" &&
                                                dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                .dataContent != "" &&
                                                dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                .dataContent != "null"
                                            ) {
                                                //因为sql返回的是array字符串，所以要进行处理
                                                var checkboxArr = eval(
                                                    dataBody[findUser].dataDetailsList[findUserAnsAll]
                                                    .dataContent
                                                );
                                                // 将数组拆开拿每一个值存入对应位置
                                                for (var z in checkboxArr) {
                                                    arrAns[findQuestionAll].push(checkboxArr[z].value);
                                                }
                                            } else {
                                                //空值则赋一个“”
                                                ans = "";
                                                arrAns[findQuestionAll].push(ans);
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        //清空[]答案
                        var newArrAns = arrAns.filter((item) => {
                            return item.length > 0;
                        });
                        console.log("=====《分析后的答案信息》=====");
                        console.log(newArrAns);

                        //================================================造标题titleArr============================================//
                        //================================================造选项templateContent======================================//
                        if (dataBody[0] == undefined) {
                            this.$message({
                                showClose:true,
                                message: '暂无数据哟~(#^.^#)',
                                type: 'warning'
                            });
                        } else {

                            for (
                                let findQuestionAll = 0; findQuestionAll < dataHead.length; findQuestionAll++
                            ) {
                                if (dataHead[findQuestionAll].templateName != "textbox") {
                                    var contents = eval(dataHead[findQuestionAll].templateContent);
                                    var result = contents.map((option) => option.value);
                                    templateContent.push(result);
                                    titleArr.push(dataHead[findQuestionAll].templateTitle);
                                }
                            }

                        }
                        console.log("^^^^^《分析后的题目》^^^^^");
                        console.log(titleArr);

                        console.log("^^^^^《分析后的选项》^^^^^");
                        console.log(templateContent);
                        //echarts答案格式[[10,20,30],[...],[...]]

                        //================================================答案数量信息dataValue======================================//
                        for (
                            let questionNum = 0; questionNum < templateContent.length; questionNum++
                        ) {
                            let arr = [];
                            // 循环通过filter遍历数组
                            for (let quesOption in templateContent[questionNum]) {
                                arr.push(
                                    newArrAns[questionNum].filter(
                                        (option) =>
                                        option == templateContent[questionNum][quesOption]
                                    ).length
                                );
                            }
                            dataValue.push(arr);
                        }
                        console.log("%%%%%《分析后的答案数量信息》%%%%%");
                        console.log(dataValue);
                        //----------------------------
                        for (let templateNum in templateContent) {
                            //每一个charts默认为bar
                            let types = {
                                type: "bar"
                            };
                            //将types存到数组里，这样可以单独通过下标修改对应的type以实现单个修改
                            this.chartsDefaultTypes.push(types);
                            let chartsItem = {
                                // barChart: "", // chart 对象实例
                                id: "chartsId_" + templateNum, // 为了标示 id 不同
                            };
                            arrChartsLength.push(chartsItem);

                            let Arr = [];
                            var titleValue = {};
                            var dataxValue = {};
                            var datayValue = {};

                            titleValue["title"] = titleArr[templateNum];
                            dataxValue["datax"] = templateContent[templateNum];
                            datayValue["datay"] = dataValue[templateNum];
                            Arr.push(titleValue);
                            Arr.push(dataxValue);
                            Arr.push(datayValue);
                            var newObj = {};
                            // 将[{...},{...},{...}]处理为[{...,...,...}]
                            Arr.forEach((newItem) => {
                                newObj = {
                                    ...newObj,
                                    ...newItem
                                };
                            });
                            arrOptionData.push(newObj);
                            this.chartsOption = arrOptionData;
                        }
                        // 将存有id的arr放入chartIdList供v-for使用
                        this.chartIdList = arrChartsLength;

                        var tableOptions = [];
                        for (let tableNum in templateContent) {
                            tableOptions.push([]);
                            for (let conValue in templateContent[tableNum]) {
                                let param = {};
                                param.title = templateContent[tableNum][conValue];
                                param.optionValue = dataValue[tableNum][conValue];
                                tableOptions[tableNum].push(param);
                            }
                        }
                        this.tableOption = tableOptions;
                        console.log("*****《分析后的表格信息》******");
                        console.log(this.tableOption);
                    })
                );
        },
    },
};
</script>

<style>
.charts-overflow {
    /* background: #ffffff; */
    /* 居中 */
    /* display: flex; */
    margin: 21px auto 0;
    width: 1200px;
}

.co-1-chartsHome {
    background: #ffffff;
    margin: 10px;
    display: flex;
    /* border: 5px solid #ffffff; */
}

.co-1-charts {
    flex: 2;
    margin: 10px;
    margin: auto 0;
}

.co-1-details {
    flex: 1;
    margin-right: 30px;
    margin: auto 0;
}

.co-1-button {
    height: 40px;
    /* background: #f4f4f4; */
}

.co-1-button .one {
    float: center;
}

.co-1-button .a {
    /* float: left; */
    width: 100px;
    /* height: 40px; */
    border-width: 0px;
    border-radius: 5px;
    cursor: pointer;
    /* 鼠标移入按钮范围时出现手势 */
}

.a:hover {
    background: #ffffff;
}
</style>
